.navbar {
  /* background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%); */
  /* background: linear-gradient(120deg, rgb(27, 27, 24), rgb(82, 70, 70)); */
   background: linear-gradient(9deg, rgb(76, 127, 151), rgb(24, 20, 20)); 
  /* background: transparent; */
  box-shadow: 0 0 6px rgba(0,0,0,0.9);
  height: 100 px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  
}
.but{
  font-family: 'Yomogi', cursive;
}
.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
  
}

.navbar-logo {
  color: #fff !important;
  justify-self: start;
  margin-right: 100px;
  cursor: pointer;
  text-decoration: none !important;
  font-size: 2rem;
  display: flex;
  align-items: center;
  font-family: 'Gloria Hallelujah', cursive;
  
}

/* .fa-typo3 {
  margin-left: 1.8rem;
  font-size: 1.8rem;
} */

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff !important;
  display: flex;
  align-items: center;
  text-decoration: none !important;
  padding: 0.5rem 1rem;
  height: 100%;
  font-family: 'Yomogi', cursive;
  
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
  
}

.fa-bars {
  color: #fff;
  
}

.nav-links-mobile {
  display: none;
  font-family: 'Yomogi', cursive;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;

  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
    
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
    
  }
}

